@import url('https://fonts.googleapis.com/css2?family=Open+Sans&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

.home-main {
    display: flex;
    flex-direction: column;
    background-color: inherit;
}

.home-bg-container {
    background-color: #17252a;
}

.home-bg {
    width: 100%;
    height: 750px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    background-color: #17252a;
}

.bg-image {
    height: 745px;
    width: 100%;
    opacity: 0.25;
}

.home-bg .main-text {
    margin-top: 0;
    width: 700px;
    color: #def2f1;
    position: absolute;
    top: 200px;
    left: 35%;
    word-wrap: break-word;
}

.main-text .text-first {
    font-size: 3em;
    margin-bottom: 10px;
}

.main-text .text-second {
    font-size: 1.7em;
}

@media (max-width: 1080px) {
    .main-text .text-first {
        font-size: 1.9em;
        margin-bottom: 10px;
    }  
    .main-text .text-second {
        font-size: 1.6em;
    }
    .home-bg .main-text {
        width: 500px;
    }
}

@media (max-width: 780px) {
    .main-text .text-first {
        font-size: 1.5em;
        margin-bottom: 10px;
    }  
    .main-text .text-second {
        font-size: 1.1em;
    }
    .home-bg .main-text {
        width: 300px;
    }
}

@media (max-width: 500px) {
    .home-bg .main-text {
        left: 20%;
    }
}

.home-container {
    margin: 100px 80px 100px 80px;
    display: flex;
    flex-direction: column;
    height: auto;
}

.info {
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: center;
}

.image-container {
    margin-right: 80px;
    display:flex;
    flex-direction: column;
    justify-content: space-around;
    opacity: 1;
    position: relative;
    right: 0;
    transition: 0.7s ease-out;
}

.image {
    position: relative;
    height: 500px;
    width: 375px;
    border-radius: 5px;
    background-image: url('../assets/profilePic.JPG');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    transition: 0.7s ease-out;
}

.image::before {
    content: "";
    position: absolute;
    top: 30px;
    left: 30px;
    right: -30px;
    bottom: -30px;
    border-radius: 5px;
    border: 10px solid #3aafa9;
    z-index: -1;
  }

.image-container.slide-right {
    opacity: 0;
    position: relative;
    right: 100px;
}

.image-container.slide-right .image {
    transform: rotate(360deg);
    box-shadow: none;
}

.photo-credits {
    margin-bottom: 0;
    padding-left: 20px;
    color: rgba(0, 0, 0, 0.527);
    font-size: small;
}

.photo-credits a {
    color: inherit;
}

.intro {
    max-width: 600px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    opacity: 1;
    position: relative;
    left: 0;
    transition: 0.7s ease-out;
}

.intro h1 {
    color: #3aafa9;
    font-family: 'Roboto', sans-serif;
    font-size: 3em;
}

.intro p{
    font-size: 1.2em;
    line-height: 1.5;
    font-family: 'Roboto', sans-serif;
    color: #def2f1;
    text-align: justify;
}

.intro > .resumeButton {
    background-color: #3aafa9;
    display: flex;
    width: 180px;
    height: 45px;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    text-decoration: none;
    font-size: 1.2em;
    color: #17252a;
    padding: 0 auto;
    font-weight: 600;
    box-shadow: 2px 2px 5px 2px rgba(28, 102, 100, .2);
    transition: background-color 250ms ease-in;
    transition: color 200ms ease-in;
}

.resumeButton:hover {
    background-color: #1c6664;
    color: #def2f1;
}

.intro.slide-left {
    opacity: 0;
    position: relative;
    left: 100px;
}

@media (max-width: 900px) {
    .info {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .info .image-container{
        margin-right: 0;
        margin-bottom: 30px;
    }
}

@media (max-width: 400px) {
    .home-container {
        margin: 30px;
    }
    .image-container{
        margin-top: 20px;
    }
    .image {
        height: 275px;
        width: 275px;
    }
    .intro p {
        font-size: 1em;
    }
}

.skill-container {
    background-color: #17252a;
    background-image: url("../assets/skillsbg.jpg");
    background-size: cover;
    background-attachment: fixed;
}

.skill-img-credits {
    margin: 0 auto 20px 10px;
    color: #def2f1;
    opacity: 0.5;
}

.skill-img-credits a {
    color: #32b8b3;
}

.projects-container {
    height: auto;
    width: 100%;
}